(async function () {
  const response = await fetch("/game/resources/models/objs/Handgun_obj.obj");
  const text = await response.text();
  console.log(text);
  const lines = text.split("\n");
  const regexKeyword = /(w*)(?: )*(.*)/;

  let vertexData = {
    positionCordinate: [[0, 0, 0]],
    textureCordinate: [],
    normalCordinate: [],
  };

  let addMethods = {
    v(data) {
      vertexData.positionCordinate.push(data);
    },
    vt(data) {
      vertexData.textureCordinate.push(data);
    },
    vn(data) {
      vertexData.normalCordinate.push(data);
    },
    f(data) {
      // add the data in webgl from vertex data stored
    },
  };

  for (let i = 0, length = 6; i < length; i++) {
    let line = lines[i].trim();
    if (line === "" || line.startsWith("#")) {
      continue;
    }
    let result = regexKeyword.exec(line);
    if (!result) {
      continue;
    }
    const [, keyword, unparsedArgs] = result;
    let datas = line.split(/\s+/).slice(1);
    const handlerFunction = addMethods[keyword];
    if (!handlerFunction) {
      console.warn("identifiec keyword", keyword);
    }
    handlerFunction(datas);
  }
})();
